'use strict';

import 'normalize.css';
import '../css/style.css';

import Widget from './widget/widget';

// –ü–æ–ª—É—á–∞—é –∂–∏–≤—É—é –∫–æ–ª–µ–∫—Ü–∏—é <script> 
let scripts = document.getElementsByTagName('script');
let myScript = scripts[scripts.length - 1];
// –° –ø–æ–º–æ—â—å—é —Ä–µ–≥—É–ª—è—Ä–Ω–æ–≥–æ –≤—ã—Ä–∞–∂–µ–Ω–∏—è —É–¥–∞–ª—è—é –ª–µ–≤—É—é —á–∞—Å—Ç—å url
let queryString = myScript.src.replace(/^[^\?]+\??/,'');

let params = parseQuery(queryString);

function parseQuery (query){

    let Params = new Object();
    
    if (!query){
        return Params; 
    }

    let Pairs = query.split(/[;&]/);
    
    for (let i = 0; i < Pairs.length; i++ ){

        let KeyVal = Pairs[i].split('=');

        if (!KeyVal || KeyVal.length != 2) continue;

        let key = KeyVal[0];
        let val = KeyVal[1];

        Params[key] = val;
    }
    return Params;
}

let widget = new Widget;
widget.init(params.position);